*{
  margin:0;
  box-sizing: border-box;
  background: blanchedalmond;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  position: absolute;
  top:60%;
  left:50%;
  transform: translate(-50%,-50%);
  
  width: 50%;
  height: 80%;
  background: rgb(214, 183, 99);
  color:rgb(62, 0, 90);
  border-radius: 10px;
}
form{
  
  position: absolute;
  top:-20%;
  left: 40%;
  

  transform: translateX(-50%);
}
input{
  width: 20vw;
  height: 5vw;
  font-size: 20px;
}
input:hover{
  box-shadow:0 0 10px 1px crimson;
}
.todos{
  height: 100px;
  width: 100px;
  background: rgba(206, 78, 142, 0.884);
}
#butt{
 position: absolute;
  right: 10%;
  top:-20%;
  height: 10vh;
  width: 10vw;
  background:rgb(192, 8, 85);
  z-index: -1;
}
#butt:hover{
  box-shadow:0 0 10px 1px crimson;
}
#to{
  background: rgb(214, 183, 99);
  size: 20vh;
  font-size: 20px;
  border-radius: 5px;
  
  
}
#to>div{
  float: right;
  background: chocolate;
  margin-bottom:10px;
  border-radius: 5px;
  width: 90%;
  text-align: center;
}
#to>div:hover{
  background: aquamarine;
  color: black;
}
h1{

  font-size: 4vw;
  color: bisque;
  background: rosybrown;
}
#h1wrap{
  word-wrap: break-word;
  top:-20%;
  width: 20vw;
  height: 100%;
  position: fixed;
  left:-50%;
  text-align: center;
  word-wrap: break-word;
  overflow-wrap: break-word;

}h1:hover{
  background: brown;
}